import React, {useState} from "react";
import cssStyle from "./BurgerNav.module.scss"
import Link from "react-scroll/modules/components/Link";


export const BurgerNav = () => {
    let [buttonOn, setButtonOn] = useState(false)
    let [active, setActive] = useState(false)
    const toggleNav = () => {
        setButtonOn(!buttonOn)
        setActive(!active)

    }


    return (
        <nav className={cssStyle.burgerNav}>

            <div className={buttonOn ? `${cssStyle.showNav} ${cssStyle.burgerNavItems}` : cssStyle.burgerNavItems}>
                <Link
                    activeClass={cssStyle.active}
                    to="main"
                    spy={true}
                    smooth={true}
                    offset={0}
                    duration={500}>Main</Link>
                <Link
                    activeClass={cssStyle.active}
                    to="skills"
                    spy={true}
                    smooth={true}
                    offset={10}
                    duration={500}>Skills</Link>
                <Link
                    activeClass={cssStyle.active}
                    to="projects"
                    spy={true}
                    smooth={true}
                    offset={10}
                    duration={500}>Projects</Link>
            </div>



<svg className={`${cssStyle.ham} ${cssStyle.hamRotate} ${cssStyle.ham1} ${active ? cssStyle.active : ""}`} viewBox="0 0 100 100" 
width="80" onClick={toggleNav}>
  <path
        className={`${cssStyle.line} ${cssStyle.top}`}
        d="m 30,33 h 40 c 0,0 9.044436,-0.654587 9.044436,-8.508902 0,-7.854315
         -8.024349,-11.958003 -14.89975,-10.85914 -6.875401,1.098863 
         -13.637059,4.171617 -13.637059,16.368042 v 40" />
  <path
        className={`${cssStyle.line} ${cssStyle.middle}`}
        d="m 30,50 h 40" />
  <path
        className={`${cssStyle.line} ${cssStyle.bottom}`}
        d="m 30,67 h 40 c 12.796276,0 15.357889,-11.717785 15.357889,
        -26.851538 0,-15.133752 -4.786586,-27.274118 -16.667516,-27.274118 
        -11.88093,0 -18.499247,6.994427 -18.435284,17.125656 l 0.252538,40" />
</svg>

        </nav>
    )
}